    .tg {
        display: inline-block;
        width: 13px;
        height: 13px;
        background: url('../images/icons/telegram.svg') no-repeat center;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: url('../images/icons/telegram-2.svg') no-repeat center;
            transition: all 0.3s ease-in-out;
        }
    }
    
    .fb {
        display: inline-block;
        width: 7px;
        height: 15px;
        background: url('../images/icons/fb.svg') no-repeat center;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: url('../images/icons/fb-2.svg') no-repeat center;
            transition: all 0.3s ease-in-out;
        }
    }
    
    .instagram {
        display: inline-block;
        width: 15px;
        height: 15px;
        background: url('../images/icons/instagram.svg') no-repeat center;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: url('../images/icons/instagram-2.svg') no-repeat center;
            transition: all 0.3s ease-in-out;
        }
    }
    
    .youtube {
        display: inline-block;
        width: 18px;
        height: 13px;
        background: url('../images/icons/youtube.svg') no-repeat center;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: url('../images/icons/youtube-2.svg') no-repeat center;
            transition: all 0.3s ease-in-out;
        }
    }
    
    .c-icon {
        position: relative;
        margin-left: 25px;
        &::after {
            content: "";
            position: absolute;
            left: -25px;
            top: 5px;
            width: 10px;
            height: 10px;
            background: url('../images/icons/call.svg') no-repeat center;
        }
    }
    
    .back-link {
        display: inline-flex;
        align-items: center;
        font-size: 16px;
        color: $white;
        text-decoration-line: underline;
        transition: all 0.3s ease-in-out;
        svg {
            margin-right: 20px;
            width: 23px;
            height: 13px;
            stroke: $white;
            vertical-align: middle;
            transition: all 0.3s ease-in-out;
        }
        &:hover {
            color: $yellow;
            transition: all 0.3s ease-in-out;
            svg {
                stroke: $yellow;
                transition: all 0.3s ease-in-out;
            }
        }
    }
    
    .close-popup {
        position: absolute;
        display: block;
        top: 10px;
        right: 10px;
        width: 30px;
        height: 30px;
        cursor: pointer;
        &::after,
        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 14px;
            width: 2px;
            height: 100%;
            background: $black;
            transform: rotate(-45deg);
        }
        &::before {
            transform: rotate(45deg);
        }
    }