.header {
    &-top {
        background: #2A2525;
        @include mobile {
            display: none;
        }
    }
    &-flex {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 12px 0 13px 0;
    }
    &-bottom {
        display: flex;
        justify-content: space-between;
        width: 100%;
        background: #fff;
        @include mobile {
            display: none;
        }
    }
}

.social {
    display: flex;
    flex-wrap: wrap;
    li {
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
        a {
            width: 35px;
            height: 35px;
            border: 1px solid $grey;
            border-radius: 2px;
            transition: all 0.3s ease-in-out;
            &:hover {
                background-color: $yellow;
                border: 1px solid $yellow;
            }
            @include desktop {
                width: 25px;
                height: 25px;
            }
        }
    }
}

.number {
    display: flex;
    flex-wrap: wrap;
    line-height: 1.5;
    font-size: 13px;
    color: $grey;
    @include desktop {
        font-size: 12px;
    }
    span {
        color: #fff;
    }
    li {
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
}

.exchange {
    display: flex;
    li {
        position: relative;
        margin-right: 30px;
        font-weight: 600;
        font-size: 13px;
        color: $white;
        text-transform: uppercase;
        &:last-child {
            margin-right: 0;
        }
        &:not(:last-child) {
            &::after {
                content: "";
                position: absolute;
                right: -15px;
                top: 0;
                width: 1px;
                height: 15px;
                background: #fff;
            }
        }
        @include desktop {
            font-size: 12px;
        }
        font {
            font-weight: 400;
        }
        span {
            color: $yellow;
        }
    }
}

.lang {
    display: flex;
    li {
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
        a {
            display: inline-block;
            font-size: 16px;
            font-weight: 400;
            color: $grey;
            text-decoration: none;
            transition: all 0.3s ease-in-out;
            &:hover {
                color: $yellow;
                transition: all 0.3s ease-in-out;
            }
            @include desktop {
                font-size: 14px;
            }
        }
    }
    .active {
        a {
            font-weight: 600;
            color: $yellow;
        }
    }
}

nav {
    position: relative;
    z-index: 10;
    ul {
        display: flex;
        justify-content: space-between;
        li {
            &>a {
                display: inline-block;
                padding: 34px 0;
                font-size: 15px;
                color: #000;
                text-decoration: none;
                transition: all 0.3s ease-in-out;
                &:hover {
                    color: $yellow;
                    transition: all 0.3s ease-in-out;
                }
            }
        }
        .submenu {
            &>a {
                position: relative;
                display: inline-block;
                font-weight: 600;
                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: -12px;
                    width: 22px;
                    height: 22px;
                    margin: 0 auto;
                    background: url('../images/icons/drop.svg') no-repeat center;
                    z-index: 4;
                }
            }
            &:hover {
                .sublist {
                    opacity: 1;
                    z-index: 2;
                    visibility: visible;
                    transition: opacity 0.3s ease-in-out;
                }
            }
        }
        .sublist {
            position: absolute;
            left: 0;
            top: 101%;
            width: 100%;
            padding: 45px 50px;
            background: #fff;
            box-shadow: 0px 30px 20px rgba(0, 0, 0, 0.25);
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.3s ease-in-out;
            li {
                position: relative;
                margin-right: 46px;
                &:last-child {
                    margin-right: 0;
                }
                &:not(:last-child) {
                    &::after {
                        content: "";
                        position: absolute;
                        top: -10px;
                        right: -46px;
                        width: 1px;
                        height: 40px;
                        background: $grey;
                    }
                }
            }
            a {
                display: inline-block;
                padding: 0;
                font-weight: 600;
                font-size: 20px;
                text-transform: uppercase;
                transition: all 0.3s ease-in-out;
                &:hover {
                    color: $red;
                    transition: all 0.3s ease-in-out;
                }
            }
        }
    }
}

.mobile-header {
    display: none;
    @include mobile {
        position: relative;
        display: block;
        z-index: 10;
        &-top {
            padding: 20px 0;
            background: $black;
            .container {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            .number {
                padding-right: 10px;
                .c-icon {
                    margin-left: 20px;
                    &::after {
                        left: -20px;
                    }
                }
            }
        }
        &-bottom {
            background: $black;
            padding-bottom: 30px;
            .container {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: center;
            }
            .social {
                padding-bottom: 20px;
                li {
                    a {
                        width: 30px;
                        height: 30px;
                    }
                }
            }
            .lang {
                padding-bottom: 20px;
            }
            .exchange {
                width: 100%;
                justify-content: center;
                li {
                    font-size: 11px;
                }
            }
        }
        &-menu {
            position: absolute;
            display: none;
            left: 0;
            right: 0;
            width: 100%;
            background: #fff;
        }
        nav {
            ul {
                flex-direction: column;
                li {
                    text-align: center;
                    a {
                        padding: 20px 0;
                        font-size: 20px;
                    }
                }
            }
            .submenu {
                &>a {
                    &::after {
                        left: unset;
                        right: -30px;
                        top: 0;
                        bottom: 0;
                        margin: auto;
                    }
                }
            }
            .sublist {
                display: none;
                opacity: 1;
                position: relative;
                visibility: visible;
                box-shadow: none;
                padding: 0;
                &::after {
                    content: "";
                    position: absolute;
                    left: -100%;
                    right: 0;
                    top: 0;
                    margin: auto;
                    width: 1024px;
                    height: 100%;
                    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25), inset 0px 1px 0px #4D3930;
                }
                li {
                    margin: 0;
                    &:not(:last-child) {
                        border-bottom: 1px solid #ccc;
                        &::after {
                            display: none;
                        }
                    }
                    a {
                        font-size: 15px;
                    }
                }
            }
        }
    }
}

.mobile-btn {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 16px;
    &::after,
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 5px;
        background: $white;
    }
    &::before {
        top: unset;
        bottom: 0;
    }
    &--open {
        &::before {
            top: 5px;
            transform: rotate(-45deg);
        }
        &::after {
            top: 5px;
            transform: rotate(45deg);
        }
    }
}