.slider {
    position: relative;
    display: block;
    min-height: 850px;
    background-color: $black;
    overflow: hidden;
    @media screen and (max-width: 1200px) {
        min-height: 695px;
    }
    &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, #4D3830 0%, rgba(65, 51, 46, 0) 100%);
        z-index: 1;
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.01;
        background: url('../images/icons/zh.png') repeat;
        z-index: 2;
    }
    &-catalog {
        position: relative;
        z-index: 5;
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease-in-out;
        &-wrap {
            margin-left: calc(50% - 400px);
            padding-left: 55px;
            border-left: 1px solid $brown;
        }
        &-items {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 30px;
        }
        &-item {
            display: block;
            width: 51px;
            height: 51px;
            margin-right: 20px;
            border: 3px solid $brown;
            border-radius: 50%;
            overflow: hidden;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            transition: all 0.3s ease-in-out;
            &:last-child {
                margin-right: 0;
            }
            &:hover {
                border: 3px solid #FFF9C1;
                box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.25);
                transition: all 0.3s ease-in-out;
            }
            img {
                display: block;
                width: 100%;
                height: auto;
            }
            &__close {
                position: relative;
                display: block;
                width: 100%;
                height: 100%;
                &::after,
                &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    width: 3px;
                    height: 20px;
                    margin: auto;
                    background: $brown;
                    transform: rotate(-45deg);
                    transition: all 0.3s ease-in-out;
                }
                &::before {
                    transform: rotate(45deg)
                }
                &:hover {
                    &::after,
                    &::before {
                        background: #FFF9C1;
                    }
                }
            }
        }
        &-link {
            a {
                font-weight: normal;
                font-size: 16px;
                line-height: 1.2;
                color: $brown;
                text-decoration-line: underline;
                transition: all 0.3s ease-in-out;
                &:hover {
                    color: #FFF9C1;
                    transition: all 0.3s ease-in-out;
                }
            }
        }
    }
    &-wrap {
        position: absolute;
        top: 35px;
        left: 0;
        right: 0;
        width: 1600px;
        margin: 0 auto;
        min-height: inherit;
        @media screen and (max-width: 1200px) {
            width: 1200px;
        }
    }
    &-inner {
        position: relative;
        z-index: 3;
        width: 1170px;
        margin: 0 auto;
        padding-left: 30px;
        &-text {
            width: 45%;
            color: $yellow;
            h1 {
                margin-top: 0;
                margin-bottom: 20px;
                font-weight: bold;
                font-size: 45px;
                line-height: 48px;
            }
            p {
                margin-top: 0;
                margin-bottom: 10px;
                font-weight: normal;
                font-size: 19px;
                line-height: 1.2;
            }
            .btn {
                margin-top: 20px;
                background: $white;
                color: $black;
            }
        }
    }
    .logo {
        margin-top: 25px;
        margin-bottom: 20px;
        display: inline-block;
    }
    &-svg {
        position: absolute;
        top: 36.5px;
        left: -70px;
        width: 100%;
        height: 1110px;
        z-index: 3;
        #anim {
            width: 100%;
            path {
                stroke-dasharray: 3000;
                stroke-dashoffset: 3000;
            }
        }
    }
    &-one {
        z-index: 8;
        @media screen and (max-width: 1200px) {
            width: 172px;
            height: 435px;
            right: 298px;
            top: 52px;
        }
    }
    &-two {
        z-index: 9;
    }
    &-three {
        z-index: 10;
    }
    &-one,
    &-two,
    &-three {
        position: absolute;
        width: 230px;
        height: 581px;
        right: 373px;
        top: 56px;
        opacity: 0;
        visibility: hidden;
        background-size: cover;
        background-repeat: no-repeat;
        clip-path: polygon(0px 17.4%, 100% 0px, 100% 100%, 0px 83%);
        transition: opacity 1s cubic-bezier(0.25, 0.46, 0.45, 0.94), right 1s cubic-bezier(0.25, 0.46, 0.45, 0.94), animation 1s cubic-bezier(0.25, 0.46, 0.45, 0.94), width 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        @media screen and (max-width: 1200px) {
            width: 172px;
            height: 435px;
            right: 298px;
            top: 52px;
        }
    }
    &-tooltip {
        position: absolute;
        top: 0;
        right: 0;
        width: 700px;
        height: 700px;
        @media screen and (max-width: 1200px) {
            width: 600px;
            height: 500px;
        }
    }
    @include mobile {
        min-height: 790px;
        &-catalog {
            display: none;
        }
        &-one,
        &-two,
        &-three,
        &-tooltip {
            display: none;
        }
        &-wrap {
            left: -290px;
            top: 0;
            width: 1555px;
        }
        &-svg {
            left: unset;
            height: auto;
        }
        &-inner {
            width: 320px;
            padding: 0 15px;
            text-align: center;
            &-text {
                width: 100%;
                h1 {
                    font-size: 27px;
                    line-height: 1.2;
                }
                p {
                    font-size: 16px;
                }
                .btn {
                    display: block;
                    margin-top: 50px;
                }
            }
        }
    }
}

.tooltip {
    position: absolute;
    width: 152px;
    padding: 15px 24px;
    background: #fff;
    border-radius: 6px;
    opacity: 0;
    z-index: 10;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        right: -8px;
        margin: auto;
        width: 15px;
        height: 15px;
        background: #fff;
        transform: rotate(45deg);
    }
    p {
        margin: 0;
        font-size: 20px;
        line-height: 1.2;
        color: #2B2525;
        font-weight: 400;
    }
    span {
        display: inline-block;
        margin-top: 0;
        margin-bottom: 9px;
        font-weight: bold;
        font-size: 25px;
        line-height: 1.2;
        color: #FFCC00;
    }
    &-one,
    &-two,
    &-three {
        animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 1s;
        transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }
}

.tooltip-one {
    opacity: 0;
    visibility: hidden;
    top: 300px;
    right: 500px;
    @media screen and (max-width: 1200px) {
        top: 200px;
        right: 350px;
    }
}

.tooltip-two {
    opacity: 0;
    visibility: hidden;
    top: 300px;
    right: 500px;
    @media screen and (max-width: 1200px) {
        top: 200px;
        right: 400px;
    }
}

.tooltip-three {
    opacity: 0;
    visibility: hidden;
    top: 300px;
    right: 600px;
    @media screen and (max-width: 1200px) {
        top: 200px;
        right: 500px;
    }
}

.js-animPlay {
    #anim {
        path {
            animation: animSvg 5s linear forwards;
        }
    }
    .slider-one,
    .slider-two,
    .slider-three {
        animation: opacity 2s linear both;
        animation-delay: 5s;
    }
    .slider-catalog {
        opacity: 1;
        visibility: visible;
        transition: all 0.3s ease-in-out;
        transition-delay: 5s;
    }
}

.js-hover {
    .slider-one,
    .slider-two,
    .slider-three {
        transition: all 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }
    .slider-one {
        width: 240px;
        transition-delay: 0.5s;
        @media screen and (max-width: 1200px) {
            width: 175px;
        }
    }
    .slider-two {
        right: 430px;
        transition-delay: 0.7s;
        @media screen and (max-width: 1200px) {
            right: 340px;
        }
    }
    .slider-three {
        right: 500px;
        transition-delay: 0.10s;
        @media screen and (max-width: 1200px) {
            right: 400px;
        }
    }
    .tooltip-two {
        opacity: 0;
        visibility: hidden;
        animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 0.3s;
    }
    .tooltip-three {
        opacity: 1;
        visibility: visible;
        animation: slide-in-right 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 1s;
    }
}

.js-texture-hover {
    .slider-one {
        width: 240px;
        transition-delay: 0.5s;
        @media screen and (max-width: 1200px) {
            width: 175px;
        }
    }
    .slider-two {
        right: 450px;
        transition-delay: 0.5s;
        @media screen and (max-width: 1200px) {
            right: 360px;
        }
    }
    .slider-three {
        right: 600px;
        transition-delay: 0.2s;
        @media screen and (max-width: 1200px) {
            right: 450px;
        }
    }
    .tooltip-three {
        opacity: 0;
        visibility: hidden;
        animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 0.3s;
    }
    .tooltip-two {
        opacity: 1;
        visibility: visible;
        animation: slide-in-right 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 0.5s;
    }
}

.js-pvh-hover {
    .slider-two {
        right: 550px;
        @media screen and (max-width: 1200px) {
            right: 400px;
        }
    }
    .slider-one {
        width: 245px;
        @media screen and (max-width: 1200px) {
            width: 185px;
        }
    }
    .slider-three {
        right: 600px;
        transition-delay: 0.2s;
        @media screen and (max-width: 1200px) {
            right: 450px;
        }
    }
    .tooltip-two {
        opacity: 0;
        visibility: hidden;
        animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 0.3s;
    }
    .tooltip-one {
        opacity: 1;
        visibility: visible;
        animation: slide-in-right 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation-delay: 0.5s;
    }
}