.product {
    background: $black;
    &-items {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0 100px;
        padding: 60px 0;
    }
    &-img {
        position: relative;
        width: 500px;
        height: 587px;
        border-radius: 3px;
        border: 1px solid #777474;
        overflow: hidden;
        img {
            position: absolute;
            display: block;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            width: calc(100% - 40px);
            height: calc(100% - 40px);
            object-fit: cover;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        }
    }
    &-info {
        width: 317px;
        &-desc {
            margin-top: 50px;
        }
        &-name {
            margin-bottom: 37px;
            font-weight: bold;
            font-size: 50px;
            color: $white;
        }
        &-list {
            color: $white;
            margin-bottom: 50px;
            ul {
                margin-bottom: 20px;
                li {
                    b {
                        display: inline-block;
                        margin-bottom: 10px;
                        font-weight: bold;
                        font-size: 16px;
                    }
                    span {
                        font-weight: 300;
                        font-size: 16px;
                        line-height: 1.5;
                    }
                }
            }
        }
    }
    .default-section {
        margin-bottom: 0;
    }
    &-popup {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(17, 17, 17, 0.8);
        z-index: 1000;
        display: none;
        &-wrap {
            position: absolute;
            max-width: 80%;
            max-height: 80%;
            min-width: 50%;
            min-height: 50%;
            top: 50%;
            left: 50%;
            overflow: auto;
            transform: translate(-50%, -50%);
        }
        &-bg {
            padding: 80px 20% 80px 20%;
            background: #F8F8F8;
        }
        &-form {
            &__title {
                margin-bottom: 50px;
                text-align: center;
                h5 {
                    margin-top: 0;
                    margin-bottom: 10px;
                    font-size: 45px;
                }
                p {
                    margin: 0;
                    font-weight: 300;
                    font-size: 16px;
                }
            }
            .input-group {
                &::before {
                    background: $black;
                }
                input {
                    height: 50px;
                    line-height: 50px;
                    &::placeholder {
                        color: $black;
                    }
                }
                .icon {
                    svg {
                        fill: $black;
                    }
                }
            }
        }
        .catalog-slider-items {
            display: block;
        }
        .catalog-slider-item {
            height: 545px;
        }
        .owl-nav {
            position: absolute;
            top: 32%;
            left: 0;
            right: 0;
            margin-top: 10px;
        }
        &-other {
            padding: 50px 20px 0;
            background: $black;
            &__title {
                margin-bottom: 50px;
                font-style: normal;
                font-weight: bold;
                font-size: 45px;
                color: $white;
                text-align: center;
            }
        }
    }
    @include mobile {
        &-items {
            position: relative;
            grid-template-columns: 1fr;
            grid-gap: 20px 0;
        }
        &-img {
            width: 100%;
            height: 400px;
            margin-top: 120px;
        }
        .back-link {
            position: absolute;
            top: 50px;
            left: 0;
        }
        &-info {
            &-desc {
                margin-top: 0;
            }
            &-name {
                position: absolute;
                top: 110px;
                font-size: 30px;
                left: 0;
                right: 0;
                text-align: center;
                margin-bottom: 0;
            }
        }
        &-popup {
            &-bg {
                padding: 50px 20px;
            }
            &-form {
                &__title {
                    margin-bottom: 30px;
                    h5 {
                        font-size: 25px;
                    }
                }
            }
            &-other {
                padding-bottom: 20px;
                &__title {
                    margin-bottom: 30px;
                    font-size: 24px;
                }
            }
            .catalog-slider-items {
                margin-bottom: 0;
            }
            .catalog-slider-item {
                height: auto;
            }
        }
    }
}