footer {
    padding: 50px 0;
    background: #2A2525;
    @include mobile {
        padding: 30px 0;
    }
}

.footer {
    &-items {
        display: flex;
        flex-wrap: wrap;
        flex: 0 0 100%;
        &-left {
            width: 40%;
            margin-right: 100px;
        }
        &-right {
            width: calc(60% - 100px);
        }
        &-address {
            width: 75%;
            p {
                margin-top: 0;
                margin-bottom: 20px;
                font-weight: 400;
                font-size: 16px;
                color: $white;
            }
        }
        &-number {
            margin-bottom: 40px;
            ul {
                display: flex;
                flex-direction: column;
                li {
                    margin-bottom: 10px;
                    font-weight: 600;
                    font-size: 16px;
                    color: $white;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
        &-copyright {
            font-weight: normal;
            font-size: 16px;
            line-height: 1.2;
            color: $white;
        }
        &-mobile {
            display: none;
        }
    }
    &-item {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 25px;
        &:last-child {
            margin-bottom: 0;
        }
        &-form {
            display: flex;
            flex-wrap: wrap;
        }
        &-input {
            position: relative;
            width: calc(100% - 260px);
            height: 52px;
            margin-right: 30px;
            input {
                width: 100%;
                height: 100%;
                line-height: 52px;
                padding-left: 30px;
                outline: none;
                border: none;
                color: $yellow;
                background: transparent;
                &::placeholder {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 15px;
                    color: $grey;
                }
            }
            &::before {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                background: $grey;
                z-index: 1;
            }
            &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 0;
                height: 1px;
                background: $yellow;
                z-index: 2;
                transition: all 0.3s ease;
            }
            &.focused {
                &::after {
                    width: 100%;
                    transition: all 0.3s ease;
                }
                input {
                    &::placeholder {
                        color: $yellow;
                    }
                }
                .icon {
                    svg {
                        opacity: 1;
                        fill: $yellow;
                        transition: all 0.3s ease;
                    }
                }
            }
        }
        .icon {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            width: 20px;
            height: 100%;
            svg {
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                margin: auto;
                fill: $grey;
                opacity: 0.2;
                transition: all 0.3s ease;
            }
        }
        #search {
            .footer-item-input {
                width: 100%;
                margin-right: 0;
                input {
                    padding: 0;
                }
            }
            .icon-search {
                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                width: 20px;
                height: 20px;
                padding: 0;
                margin: auto;
                border: none;
                background: none;
                svg {
                    fill: $white;
                }
            }
        }
        form {
            width: 100%;
        }
        &__between {
            justify-content: space-between;
        }
        &-social {
            display: flex;
            align-items: flex-end;
            span {
                display: inline-block;
                margin-right: 20px;
                font-weight: normal;
                font-size: 16px;
                line-height: 1.2;
                color: $white;
            }
        }
    }
    @include mobile {
        &-items {
            flex-direction: column;
            &-left,
            &-right {
                width: 100%;
                margin: 0;
            }
            &-left {
                order: 2;
            }
            &-right {
                display: flex;
                flex-direction: column;
                order: 1;
                margin-bottom: 20px;
            }
            #search {
                input {
                    padding-right: 30px!important;
                }
            }
            &-address {
                width: 100%;
                p {
                    font-size: 13px;
                }
            }
            &-number {
                margin-bottom: 20px;
                ul {
                    li {
                        font-size: 13px;
                    }
                }
            }
            &-copyright {
                font-size: 13px;
                margin-bottom: 30px;
            }
            &-mobile {
                display: block;
                padding-top: 20px;
                border-top: 1px solid $grey;
                text-align: center;
            }
        }
        &-item {
            margin-bottom: 0;
            &:nth-child(1) {
                order: 2;
            }
            &:nth-child(2) {
                order: 1
            }
            &:nth-child(3) {
                order: 3;
            }
            &-link {
                display: none;
            }
            &-input {
                width: 100%;
                margin-right: 0;
                margin-bottom: 20px;
                input {
                    &::placeholder {
                        font-size: 14px;
                    }
                }
            }
            &-btn {
                display: block;
                width: 100%;
                margin-bottom: 50px;
                .btn {
                    width: 100%;
                    color: #fff;
                    background: $red;
                    border-color: $red;
                    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
                    span {
                        border: 1px solid $yellow;
                        background: $yellow;
                        &::after,
                        &::before {
                            background: $black;
                        }
                    }
                }
            }
            &-social {
                flex-wrap: wrap;
                flex-direction: column;
                width: 100%;
                align-items: baseline;
                span {
                    display: block;
                    margin-right: 0;
                    margin-bottom: 20px;
                }
                .social {
                    width: 100%;
                    justify-content: space-between;
                    li {
                        a {
                            width: 50px;
                            height: 50px;
                        }
                    }
                }
            }
        }
    }
}