.about-page {
    .slider {
        min-height: 549px;
        &-svg {
            width: 1542px;
            right: -1500px;
            margin: auto;
            top: -115px;
            left: 0;
        }
        &-wrap {
            top: 0;
            width: 100%;
        }
        .logo {
            margin-top: 53px;
            margin-bottom: 53px;
        }
        &-inner {
            padding-left: 0;
            &-text {
                color: $white;
            }
        }
        @include mobile {
            &-wrap {
                width: 320px;
                left: 0;
            }
            &-svg {
                left: -345px;
                width: 1555px;
                right: unset;
                top: -124px;
            }
        }
    }
}

.default-section {
    margin-bottom: 70px;
    img {
        max-width: 100%;
        height: auto;
    }
    h1,
    h2,
    h3,
    h4,
    h5 {
        margin-top: 0;
        margin-bottom: 20px;
        color: $black;
        font-weight: bold;
        line-height: 1.2;
    }
    h1 {
        font-size: 50px;
    }
    h2 {
        font-size: 45px;
    }
    h3 {
        font-size: 35px;
    }
    h4 {
        font-size: 20px;
    }
    h5 {
        font-size: 18px;
    }
    p {
        font-size: 16px;
        line-height: 22px;
    }
    ul {
        margin-bottom: 20px;
        margin-top: 20px;
        li {
            position: relative;
            padding-left: 15px;
            font-size: 16px;
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
            &::after {
                position: absolute;
                content: "";
                height: 5px;
                width: 5px;
                background: $yellow;
                border-radius: 50%;
                left: 2px;
                top: 1px;
                bottom: 0;
                margin: auto;
            }
        }
    }
    table {
        display: block;
        border-spacing: 1px;
        margin: 50px 0;
        max-width: 100%;
        overflow-x: auto;
        th {
            font-weight: bold;
            font-size: 20px;
            line-height: 1.2;
            text-align: left;
            color: #000;
        }
        tr,
        th,
        td {
            width: 33.3%;
            border: 1px solid #000;
            padding: 15px;
        }
        td {
            font-weight: normal;
            font-size: 16px;
            color: #000;
        }
    }
    i,
    b,
    u {
        display: inline-block;
        line-height: 1.2;
        font-size: 16px;
        color: $black;
        margin-bottom: 10px;
    }
    a {
        display: inline-block;
        line-height: 1.2;
        font-size: 16px;
        color: $red;
        &:hover {
            text-decoration: none;
        }
    }
    @include mobile {
        img {
            margin-bottom: 10px;
        }
        h1 {
            font-size: 25px;
        }
        h2 {
            font-size: 23px;
        }
        h3 {
            font-size: 18px;
        }
        h4 {
            font-size: 12px;
        }
        p {
            font-size: 12px;
            line-height: 1.5;
        }
        h5 {
            font-size: 10px;
        }
        ul {
            li {
                font-size: 12px;
            }
        }
        table {
            margin: 25px 0;
            tr,
            td,
            th {
                font-size: 12px;
            }
        }
        b,
        i,
        u,
        a {
            font-size: 12px;
        }
    }
}