*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    box-sizing: border-box;
}

body {
    min-width: 320px;
    font-family: $gotham;
    font-size: 16px;
    color: $black;
}

button {
    outline: none;
}

.wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 0 auto
}

.content {
    overflow-x: hidden;
}

a:active,
a:hover,
a:focus {
    outline: none;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.container {
    margin: 0 auto;
    width: 1170px;
    @include desktop {
        padding: 0 15px;
        width: 100%;
    }
    @include mobile {
        width: 320px;
    }
}

.inner-page,
.about-page {
    .content {
        min-height: calc(100vh - 328px);
    }
    .wrapper {
        padding: 70px 0;
    }
}

.title {
    h1 {
        margin: 0;
        font-weight: 600;
        font-size: 50px;
        color: $black;
        text-align: center;
    }
    @include mobile {
        margin-bottom: 50px!important;
        h1 {
            font-size: 35px;
        }
    }
}

.owl {
    &-dots {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin-top: 35px;
    }
    &-dot {
        width: 20px;
        height: 20px;
        margin-right: 10px;
        border-radius: 50%;
        border: 3px solid #000;
        background: transparent;
        cursor: pointer;
        transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
        &:last-child {
            margin-right: 0;
        }
        &.active {
            border-color: $yellow;
            background: $yellow;
            transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
        }
    }
}

.btn {
    position: relative;
    display: inline-block;
    padding: 0 35px;
    font-weight: 600;
    font-size: 13px;
    line-height: 50px;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
    border: 1px solid #fff;
    border-radius: 2px;
    background: transparent;
    transition: all 0.3s ease-in-out;
    span {
        position: absolute;
        display: inline-block;
        top: 0;
        bottom: 0;
        right: -11px;
        width: 22px;
        height: 22px;
        margin: auto;
        border-radius: 50%;
        background: $yellow;
        transition: all 0.3s ease-in-out;
        &::after,
        &::before {
            content: "";
            position: absolute;
            top: -1px;
            bottom: 1px;
            left: 1px;
            right: 0;
            margin: auto;
            width: 2px;
            height: 6px;
            background: #000;
            border-radius: 2px;
            transform: rotate(-45deg);
            transition: all 0.3s ease-in-out;
        }
        &::before {
            transform: rotate(45deg);
            top: 5px;
        }
    }
    &:hover {
        color: $black;
        background: #fff;
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
        transition: all 0.3s ease-in-out;
        span {
            background: $yellow;
            transition: all 0.3s ease-in-out;
            &::after,
            &::before {
                background: $black;
                transition: all 0.3s ease-in-out;
            }
        }
    }
    &-white {
        padding: 0 56px;
        span {
            border: 1px solid $white;
            background: $black;
            &::after,
            &::before {
                background: $white;
            }
        }
        &:hover {
            span {
                border: 1px solid $yellow;
            }
        }
    }
    &-black {
        border: 1px solid $black;
        color: $black;
        padding: 0 53px;
        &:hover {
            background: $red;
            border: 1px solid $red;
            color: $white;
            span {
                border: 1px solid $yellow;
            }
        }
        span {
            background: $white;
            border: 1px solid $black;
        }
    }
    &-active {
        padding: 0 53px;
        background: #fff;
        color: #2B2525;
    }
}

.owl-nav {
    .owl-prev,
    .owl-next {
        position: absolute;
        background: $yellow;
        border: none;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        box-shadow: 0px 12.0988px 30.2469px rgba(0, 0, 0, 0.25);
        transition: all 0.3s ease-in-out;
        svg {
            position: absolute;
            stroke: $black;
            width: 11px;
            height: 19px;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            transition: all 0.3s ease-in-out;
        }
        &:hover {
            background: $red;
            transition: all 0.3s ease-in-out;
            svg {
                stroke: $white;
                transition: all 0.3s ease-in-out;
            }
        }
    }
    .owl-prev {
        left: 0;
    }
    .owl-next {
        right: 0;
    }
}

.input-group {
    position: relative;
    input {
        padding-left: 30px;
        width: 100%;
        height: 40px;
        line-height: 40px;
        outline: none;
        border: none;
        color: $yellow;
        background: transparent;
        box-sizing: border-box;
        &::placeholder {
            font-size: 16px;
            line-height: 15px;
            color: $grey;
        }
    }
    &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: $grey;
        z-index: 1;
    }
    &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 0;
        height: 2px;
        background: $yellow;
        z-index: 2;
        transition: all 0.3s ease;
    }
    &.focused {
        &::after {
            width: 100%;
            transition: all 0.3s ease;
        }
        input {
            &::placeholder {
                color: $yellow;
            }
        }
        .icon {
            svg {
                opacity: 1;
                fill: $yellow;
                transition: all 0.3s ease;
            }
        }
    }
    .icon {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 20px;
        height: 100%;
        svg {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            margin: auto;
            fill: $grey;
            opacity: 0.2;
            transition: all 0.3s ease;
        }
    }
}

.error {
    animation: shake 0.3s linear both;
    &::before {
        background: $red!important;
    }
    &::after {
        opacity: 0!important;
    }
    .icon {
        svg {
            fill: $red!important;
            opacity: 1!important;
        }
    }
    input {
        &::placeholder,
        &:focus {
            color: $red!important;
        }
    }
}

.date {
    display: inline-block;
    position: relative;
    margin-left: 25px;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.2;
    color: $black;
    &::after {
        content: "";
        position: absolute;
        top: 0px;
        left: -25px;
        width: 16px;
        height: 16px;
        background: url('../images/icons/date.svg') no-repeat center;
    }
}

.crumbs {
    margin: 25px 0;
    ul {
        display: flex;
        flex-wrap: wrap;
        li {
            position: relative;
            font-weight: normal;
            font-size: 16px;
            line-height: 1.2;
            color: $red;
            text-decoration-line: underline;
            cursor: default;
            &:not(:last-child) {
                margin-right: 20px;
                &::after {
                    content: "";
                    position: absolute;
                    display: block;
                    top: 3px;
                    right: -10px;
                    width: 1px;
                    height: 15px;
                    background: $black;
                    transform: rotate(35deg);
                }
            }
            a {
                display: inline-block;
                font-weight: normal;
                font-size: 16px;
                line-height: 1.2;
                color: $black;
                text-decoration-line: underline;
            }
        }
    }
    @include mobile {
        ul {
            li {
                font-size: 12px;
                &:not(:last-child) {
                    &::after {
                        height: 10px;
                    }
                }
                a {
                    font-size: 12px;
                }
            }
        }
    }
}

.swipe_table {
    display: inline-block;
    width: 35px;
    height: 41px;
    background: url('../images/icons/swipe.svg') no-repeat center;
}

.swipe-table {
    display: none;
    position: absolute;
    top: 0;
    width: 100%;
    height: 150px;
    text-align: center;
    background: 0 0;
    z-index: 2;
}

.swipe-table .swipe_table {
    animation: swipe 2s infinite;
    max-width: 100%;
}