.inner-page {
    .catalog {
        padding-top: 41px;
        background: $black;
        h1 {
            margin-top: 0;
            margin-bottom: 30px;
            color: $white;
            font-size: 50px;
        }
        &-wrapper {
            padding-bottom: 70px;
        }
        &-slider {
            &-items {
                display: grid;
                grid-template-columns: repeat(4, 1fr);
                grid-gap: 0 30px;
            }
            &-item {
                height: 445px;
            }
            &-link {
                background: #2a2525;
                &:hover {
                    z-index: 2;
                }
            }
        }
        &-btn {
            margin-top: 30px;
            text-align: center;
        }
        &-seo {
            padding-top: 56px;
            padding-bottom: 42px;
            background: $white;
        }
        .default-section {
            margin-bottom: 0;
        }
        @include mobile {
            h1 {
                font-size: 25px;
                text-align: center;
            }
            &-slider {
                &-items {
                    grid-template-columns: 1fr;
                    grid-gap: 30px;
                }
                &-item {
                    height: auto;
                    p {
                        margin-bottom: 0;
                    }
                }
                &-link {
                    .btn {
                        display: none;
                    }
                }
            }
            &-btn {
                .btn {
                    width: 100%;
                }
            }
            &-seo {
                padding: 40px 0;
            }
        }
    }
}