.benefit {
    padding: 60px 0;
    &-title {
        margin-bottom: 60px;
    }
    &-items {
        display: grid;
        grid-template-columns: repeat(3, 3fr);
        grid-gap: 30px;
    }
    &-item {
        display: block;
        max-width: 370px;
        padding: 30px;
        text-align: center;
        border: 1px solid #e5e5e5;
        border-radius: 3px;
        transition: all 0.3s ease-in-out;
        &:nth-child(3n) {
            margin-right: 0;
        }
        &:hover {
            border: 1px solid $yellow;
            background: $yellow;
            transition: all 0.3s ease-in-out;
        }
        &-icon {
            display: block;
            margin-bottom: 20px;
            .icon {
                display: inline-block;
                width: 115px;
                height: 110px;
                margin: 0 auto;
                background-position: center;
                background-repeat: no-repeat;
            }
        }
        &-title {
            display: block;
            margin-bottom: 20px;
            cursor: default;
            p {
                margin: 0;
                font-weight: bold;
                font-size: 20px;
                line-height: 19px;
                text-transform: uppercase;
            }
        }
        &-txt {
            font-weight: normal;
            font-size: 16px;
            color: #000;
            cursor: default;
        }
    }
    .owl-nav {
        position: absolute;
        left: 0;
        top: 40%;
        margin-top: 10px;
        right: 0;
        .owl-prev {
            left: -15px;
        }
        .owl-next {
            right: -15px;
        }
    }
    @include mobile {
        &-item {
            padding: 30px 40px;
            background: $yellow;
            border-color: $yellow;
            &-title {
                p {
                    font-size: 16px;
                }
            }
            &-txt {
                font-size: 12px;
                line-height: 1.2;
            }
        }
    }
}

.catalog-slider {
    padding: 60px 0;
    background: #2A2525;
    &-title {
        margin-bottom: 31px;
        h1 {
            color: #fff;
        }
    }
    &-items {
        display: flex;
        flex-wrap: wrap;
        @include desktop {
            padding: 0 15px;
        }
    }
    &-item {
        position: relative;
        width: 270px;
        height: 545px;
        margin-top: 10px;
        // margin-right: 30px;
        margin-bottom: 30px;
        @include desktop {
            width: 100%;
        }
        img {
            display: block;
            width: 100%;
            height: auto;
        }
        p {
            margin-top: 0;
            margin-bottom: 30px;
            font-weight: bold;
            font-size: 20px;
            line-height: 19px;
            color: $white;
            transition: all 0.3s ease-in-out;
        }
        @include desktop {
            .btn {
                padding: 0 25px;
            }
        }
    }
    &-link {
        position: absolute;
        left: 0;
        top: 0;
        display: inline-block;
        width: 100%;
        height: 445px;
        padding: 17px 17px 42px;
        border: 1px solid #555151;
        border-radius: 3px;
        text-align: center;
        text-decoration: none;
        overflow: hidden;
        transition: all 0.3s ease-in-out;
        &:hover {
            top: -10px;
            height: 536px;
            box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.5);
            border: 1px solid $yellow;
            transition: all 0.3s ease-in-out;
            .catalog-slider-img {
                box-shadow: 0px 30px 30px rgba(0, 0, 0, 0.5);
                transition: all 0.3s ease-in-out;
            }
            p {
                color: $yellow;
                transition: all 0.3s ease-in-out;
            }
            .btn {
                background: $white;
                color: $black;
                transition: all 0.3s ease-in-out;
            }
        }
        @include desktop {
            height: 368px;
            &:hover {
                height: 470px;
            }
        }
    }
    &-img {
        display: block;
        margin-bottom: 40px;
        max-height: 342px;
        overflow: hidden;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transition: all 0.3s ease-in-out;
    }
    .owl-nav {
        position: absolute;
        top: 28%;
        left: 0;
        right: 0;
        margin-top: 10px;
        @include desktop {
            top: 23%;
        }
        .owl-prev {
            left: -15px;
        }
        .owl-next {
            right: -15px;
        }
    }
    .owl-stage-outer {
        overflow: visible
    }
    &-order {
        .title {
            margin-bottom: 55px;
            h1 {
                color: $white;
            }
        }
    }
    &-groups {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 30px;
    }
    &-submit {
        margin-top: 50px;
        text-align: center;
    }
    @include mobile {
        &-link {
            position: relative;
            height: auto;
            &:hover {
                top: unset;
                height: auto;
                border: 1px solid #555151;
                box-shadow: none;
                .btn {
                    background: transparent;
                    color: #fff;
                }
                p {
                    color: #fff;
                }
            }
            .btn {
                width: 100%;
            }
        }
        &-img {
            max-height: 280px;
        }
        &-items {
            padding: 0;
            margin-bottom: 50px;
        }
        &-item {
            margin: 0;
            height: auto;
        }
        .owl-nav {
            top: 35%;
        }
        &-order {
            .title {
                h1 {
                    font-size: 26px;
                }
            }
        }
        &-groups {
            grid-template-columns: 1fr;
        }
        &-submit {
            .btn {
                width: 100%;
            }
        }
        .owl-stage-outer {
            overflow: hidden;
        }
    }
}

.feedback-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(17, 17, 17, 0.8);
    z-index: 1000;
    &-wrap {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: 50%;
        max-height: 50%;
        transform: translate(-50%, -50%);
        padding: 50px;
        background: #f8f8f8;
        border-radius: 3px;
        overflow: hidden;
    }
    &-title {
        margin-bottom: 20px;
        font-weight: 600;
        font-size: 45px;
        color: $yellow;
        text-align: center;
    }
    &-txt {
        width: 80%;
        margin: 0 auto;
        font-weight: 400;
        font-size: 16px;
        color: $black;
        text-align: center;
    }
    @include mobile {
        &-wrap {
            padding: 50px 20px;
            min-width: 290px;
        }
        &-title {
            font-size: 24px;
        }
        &-txt {
            font-size: 14px;
        }
    }
}

.index-news {
    padding: 60px 0;
    background: #f8f8f8;
    .owl-nav {
        position: absolute;
        top: 30%;
        left: 0;
        right: 0;
        margin-top: 10px;
        .owl-prev {
            left: -25px;
        }
        .owl-next {
            right: -25px;
        }
        @include desktop {
            top: 23%;
            .owl-prev {
                left: -15px;
            }
            .owl-next {
                right: -15px;
            }
        }
    }
}

.news {
    &-items {
        display: grid;
        grid-template-columns: repeat(3, 3fr);
        grid-gap: 30px;
        margin-top: 50px;
        &-btn {
            margin-top: 50px;
            text-align: center;
        }
    }
    &-item {
        display: inline-block;
        text-decoration: none;
        &:hover {
            .news-item-title {
                color: $blue;
                transition: all 0.3s ease-in-out;
            }
        }
        &-img {
            display: inline-block;
            margin-bottom: 20px;
            width: 100%;
            height: 291px;
            overflow: hidden;
            img {
                display: block;
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }
        &-title {
            margin-bottom: 20px;
            font-weight: bold;
            font-size: 20px;
            line-height: 1.2;
            color: #000;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            transition: all 0.3s ease-in-out;
        }
        &-subtitle {
            font-weight: normal;
            font-size: 16px;
            line-height: 1.2;
            color: $black;
            max-height: 152px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 8;
            -webkit-box-orient: vertical;
        }
        .date {
            display: block;
            margin-bottom: 20px;
        }
    }
    @include mobile {
        &-item {
            .date {
                margin-bottom: 10px;
            }
            &-img {
                height: 228px;
            }
        }
    }
}

.gallery {
    padding: 60px 0;
    &-items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin-top: 40px;
    }
    &-item {
        margin-top: 30px;
        margin-right: 30px;
        &:nth-child(3n) {
            margin-right: 0;
        }
        &:nth-child(-n+3) {
            margin-top: 0;
        }
        a {
            position: relative;
            display: inline-block;
            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                opacity: 0;
                visibility: hidden;
                background: rgba(224, 41, 41, 0.8);
                transition: all 0.3s ease-in-out;
            }
            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                width: 52px;
                height: 52px;
                opacity: 0;
                visibility: hidden;
                z-index: 2;
                background: url('../images/icons/zoom.svg') no-repeat center;
                transition: all 0.3s ease-in-out;
            }
            &:hover {
                &::after,
                &::before {
                    opacity: 1;
                    visibility: visible;
                    transition: all 0.3s ease-in-out;
                }
            }
            img {
                display: block;
                width: 100%;
                height: auto;
            }
            &:not(:first-child) {
                margin-top: 30px;
            }
        }
        &__small {
            width: 270px;
            @media screen and (max-width: 1200px) {
                width: 225px;
            }
        }
        &__large {
            width: 570px;
            @media screen and (max-width: 1200px) {
                width: 475px;
            }
        }
    }
    @include mobile {
        &-item {
            margin: 0;
            &__small,
            &__large {
                width: 100%;
                height: 290px;
                overflow: hidden;
            }
            a {
                display: block;
                height: inherit;
            }
        }
        .owl-nav {
            position: absolute;
            left: 0;
            top: 32%;
            margin-top: 10px;
            right: 0;
            .owl-prev {
                left: -15px;
            }
            .owl-next {
                right: -15px;
            }
        }
    }
}

.inner-page {
    .slider {
        min-height: 211px;
        &-inner {
            display: flex;
            align-items: center;
            &-text {
                width: 40%;
                h1 {
                    font-size: 45px;
                    line-height: 43px;
                    color: #fff;
                }
                p {
                    display: none;
                }
            }
        }
        .logo {
            margin-right: 50px;
            margin-top: 50px;
            margin-bottom: 50px;
        }
        &-wrap {
            top: 0;
            width: 1170px;
        }
        &-svg {
            width: 1500px;
            height: 644.85px;
            right: -1000px;
            top: -351px;
            left: unset;
            @media screen and (max-width: 1200px) {
                width: 1000px;
                height: 644.85px;
                right: -483px;
                top: -225px;
                left: unset;
            }
        }
        @include mobile {
            padding-bottom: 40px;
            &-wrap {
                display: none;
            }
            &-inner {
                flex-direction: column;
                &-text {
                    width: 100%;
                    h1 {
                        font-size: 28px;
                        line-height: 1.2;
                    }
                }
            }
            .logo {
                margin-bottom: 40px;
                margin-right: 0;
            }
        }
    }
}

